import Layout from '../components/MyLayout.js'
import Link from 'next/link'

const PostLink = (props) => (
    <li>
        <Link href={'/post?title=${props.title}'}>
        <a>{props.title}</a>
        </Link>
        </li>
)

export default () => (
    <Layout>
        <h1> PayHQ </h1>
        <ul>
            <PostLink title="Home"/>
            <PostLink title="Trading Setting"/>
            <PostLink title="Transfer In/Out"/>
            <PostLink title="Your earning"/>
            </ul>
            </Layout>
)





